import React, { useEffect, useRef } from 'react';
import { Outlet, useLocation } from 'react-router-dom';
import { motion } from 'framer-motion';
import Sidebar from './Sidebar';
import Header from './Header';
import MiniPlayer from './MiniPlayer';
import BottomNav from './BottomNav';
import { usePlayerStore } from '../../store';
import { cn } from '../../lib/utils';

export default function MainLayout() {
  const { isMiniPlayerVisible } = usePlayerStore();
  const location = useLocation();
  const mainRef = useRef(null);

  // Scroll to top when route changes
  useEffect(() => {
    if (mainRef.current) {
      mainRef.current.scrollTo(0, 0);
    }
  }, [location.pathname]);

  return (
    <div className="flex h-screen bg-background overflow-hidden">
      {/* Sidebar - Hidden on mobile, visible on tablet/desktop */}
      <div className="hidden md:block">
        <Sidebar />
      </div>

      {/* Main content area */}
      <div className="flex-1 flex flex-col min-h-0 overflow-hidden">
        {/* Header - Hidden on mobile to maximize screen space */}
        <div className="hidden md:block">
          <Header />
        </div>

        {/* Page content */}
        <main
          ref={mainRef}
          className={cn(
            'flex-1 overflow-auto',
            // Desktop: padding for mini-player
            isMiniPlayerVisible && 'md:pb-24',
            // Mobile: padding for bottom nav + mini-player
            isMiniPlayerVisible ? 'pb-32' : 'pb-16'
          )}
        >
          <motion.div
            key={location.pathname}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.2 }}
            className={cn(
              'min-h-full',
              // Responsive padding: mobile gets less, desktop gets more
              'p-4 sm:p-6 md:p-6'
            )}
          >
            <Outlet />
          </motion.div>
        </main>
      </div>

      {/* Bottom Navigation - Mobile only */}
      <BottomNav />

      {/* Mini Player - outside AnimatePresence, stays mounted */}
      <MiniPlayer />
    </div>
  );
}
